@import url('https://fonts.googleapis.com/css2?family=Lemon&family=Salsa&display=swap');
body {
    margin: 0;
    padding: 0;
    background-color: #000000;
}

.ctn {
    max-width: 600px;
    margin: 60px auto;
    background-color: #1f1f1f;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1 {
    color: #d3d3d3;
    text-align: center;
    font-family: 'Lemon', serif;
}

h2 {
    color: #d3d3d3;
    font-family: 'Lemon', serif;
    margin: 10px;
}

form {
    margin-bottom: 20px;
    font-family: 'Salsa', cursive;
}

label {
    display: block;
    margin-bottom: 10px;
    color: #d3d3d3;
    font-family: 'Salsa', cursive;
}

.sub {
    background-color: #4caf50;
    color: #fff;
    padding: 10px 15px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-family: 'salsa', cursive;
}

.sub:hover {
    background-color: #429945;
}

.removeBtn {
    background-color: rgb(224, 16, 16);
    color: #fff;
    padding: 8px 10px;
    border: none;
    border-radius: 14px;
    cursor: pointer;
    margin: 0 10px;
    font-family: 'Salsa', cursive;
}

.removeBtn:hover {
    background-color: rgb(197, 13, 13);
}

ul {
    color: #429945;
    font-family: 'Salsa', cursive;
}